@tailwind base;
@tailwind components;
@tailwind utilities;

:root {
  font-size: 29%;
}

/* SVG-specific styles that can't be handled by Tailwind */
.w-col-diagram {
  text.text-5xl {
    font-size: 3rem !important;
  }
  
  text.text-3xl {
    font-size: 2.7rem !important;
  }
  
  line {
    stroke-width: 0.3rem !important;
  }
}
